<template>
    <div>
        <div class="big-img" @click="recordEvent($event)" >
            <div id="bigPosi" ref="recordBox">
                <img :src="list[num].bigImgurl" alt="">
            </div>
            <div  class="resetIcon" title="刷新" @click.stop.prevent="resetEvent($event)">
            </div>
            <div class="shareClass" v-for="(item,index) in originalList" :key="index" v-if="item.check" :style="item.style">
                {{item.num}}
            </div>
            <!-- <div class="secondIcon shareClass" ref='secondIcon'>2</div>
            <div class="thirdIcon shareClass" ref='thirdIcon'>3</div>
            <div class="fourthIcon shareClass" ref='fourthIcon'>4</div>
            <div class="fifthIcon shareClass" ref='fifthIcon'>5</div> -->
        </div>
        <div class="drag" ref="dragDiv"> 
            <div class="drag_text">{{confirmWords}} {{list[num].text}}</div>  
        </div>
        <div class="liucheng">
            <img src="../../assets/image/滑动拼图验证流程图.png" alt="">
        </div>
    </div>
    
</template>

<script>
    function offset(el) {
		let top = el.offsetTop;
		let left = el.offsetLeft;
		while(el.offsetParent) {
			el = el.offsetParent;
			top += el.offsetTop;
			left += el.offsetLeft;
		}
		return {
			left: left,
			top: top
		}
	}
    export default {
        data(){
            return {
                confirmWords:'请依次点击',
                list:[
                    {
                        bigImgurl:require('../../assets/image/e9144a9a43d044dd8ba032f1da723681.jpg'),// "呼" "泥" "即" "源"
                        text:' "呼" "泥" "即"'
                    },  
                    {
                        bigImgurl:require('../../assets/image/c2ee0fff8416491d92cd0612811e99b1.jpg'),//"洋" "熊" "恐" "腰" "田"
                        text:' "洋" "熊" "恐"'
                    },  
                    {
                        bigImgurl:require('../../assets/image/3f5ba67f8934404d819538ce0c6bada6.jpg'),//京欲姐渴允
                        text:' "京" "欲" "姐"'
                    }, 

                    {
                        bigImgurl:require('../../assets/image/e9144a9a43d044dd8ba032f1da723681.jpg'),// "呼" "泥" "即" "源"
                        text:' "即" "源" "呼"'
                    },  
                    {
                        bigImgurl:require('../../assets/image/c2ee0fff8416491d92cd0612811e99b1.jpg'),//"洋" "熊" "恐" "腰" "田"
                        text:' "熊" "恐" "腰"'
                    },  
                    {
                        bigImgurl:require('../../assets/image/3f5ba67f8934404d819538ce0c6bada6.jpg'),//"京" "欲" "姐" "渴" "允"
                        text:' "姐" "渴" "允"'
                    }, 

                    {
                        bigImgurl:require('../../assets/image/e9144a9a43d044dd8ba032f1da723681.jpg'),// "呼" "泥" "即" "源"
                        text:' "泥" "即" "源"'
                    },  
                    {
                        bigImgurl:require('../../assets/image/c2ee0fff8416491d92cd0612811e99b1.jpg'),//"洋" "熊" "恐" "腰" "田"
                        text:' "洋" "腰" "田"'
                    },  
                    {
                        bigImgurl:require('../../assets/image/3f5ba67f8934404d819538ce0c6bada6.jpg'),//"京" "欲" "姐" "渴" "允"
                        text:'"京" "允" "渴" '
                    }, 
                ],
                num:1,
                recordList:[],//点击记录位置的数组
                type:3,
                check:false,
                originalList:[],//有多少个字
                firstIcon:'firstIcon'
            }
        },
        methods: {


            resetEvent(){//重置
                this.num = Math.floor(Math.random()*this.list.length);
                this.originalList.forEach((item,index) => {
                    item.check = false;
                });    
                this.recordList = [];
            },
            recordEvent(e){
                console.log('jjj')
                if(this.type >this.recordList.length){
                    var num = this.recordList.length;
                    let firstIcon = this.originalList[num];
                    firstIcon.check = true;
                    firstIcon.style.zIndex += num;
                    //console.log(e);
                    let clientX = e.clientX|| e.changedTouches[0].clientX;
                    let clientY = e.clientY|| e.changedTouches[0].clientY;
                    //console.log(clientX,clientY);
                    this.scroll = document.documentElement.scrollTop || document.body.scrollTop;
                    //this.num = Math.floor(Math.random()*this.list.length);
                    this.init()
                    this.$nextTick(() =>{
                        let leftLongth = clientX-this.sImgBoxLeft-15;
                    
                        let topLongth = clientY-this.sImgBoxTop-15;

                        let toptLength = this.$refs.recordBox.getBoundingClientRect().top;
                        //console.log('dsbfjdgj'+this.$refs.recordBox.getBoundingClientRect().top)
                        if(toptLength<0){
                            topLongth = topLongth - toptLength + this.sImgBoxTop;
                        }
                        
                        firstIcon.style.left = leftLongth+"px";
                        firstIcon.style.top = topLongth+"px"  ; 
                        let obj = {
                            left:leftLongth,
                            top:topLongth
                        }  
                        console.log("leftLongth"+leftLongth,"topLongth"+topLongth)
                        let falg = true;
                        let falgNum = 0;
                        this.recordList.forEach((item,index) => {
                            Math.abs(item.left-leftLongth)
                            if(Math.abs(item.left-leftLongth)<15 && Math.abs(item.top-topLongth)<15){
                                falg = false;
                                falgNum  = index;
                                console.log("falg,falgNum")
                                console.log(falg,falgNum)
                            }else{
                                console.log("falg,falgNum")
                            }
                        });
                        if(falg){
                            this.recordList.push(obj);
                        }else{
                            
                        }                        
                        //console.log(this.recordList);
                        if(this.type == this.recordList.length){
                            console.log('去验证')
                        }
                    })    
                    
                }else{
                    console.log('去验证')
                }
            },
            init(){
                this.sImgBoxLeft = offset(this.$refs.recordBox).left; //获取小div距离屏幕左边的距离
				this.sImgBoxTop = offset(this.$refs.recordBox).top; //获取小div距离屏幕最顶部的距离
				console.log('left:'+this.sImgBoxLeft,'top:'+this.sImgBoxTop);
            },
        },
        mounted(){
            console.log(this.confirmWords[0]);
            for(let i =0;i<this.type;i++){
                let obj = {
                    check:false,
                    num:i+1,
                    style:{
                        left:'',
                        top:'',
                        'z-index':100
                    }
                }
                this.originalList.push(obj)
            }
            this.$nextTick(() =>{
                function getDivTopLength(obj) {　
                    var div = document.getElementById(obj);
                    var topLength = div.getBoundingClientRect().top; //div离屏幕上边距离（长度）
                    var bottomLength = div.getBoundingClientRect().bottom; //div离屏幕下边距离（长度）
                    var leftLength = div.getBoundingClientRect().left; //div离屏幕左边距离（长度）
                    var rightLength = div.getBoundingClientRect().right; //div离屏幕右边距离（长度）
                    return "topLength=" + topLength + "；bottomLength=" + bottomLength + "；leftLength=" + leftLength + "；rightLength=" + rightLength;
                }
                console.log(getDivTopLength("bigPosi"));
            })
        }
    }
</script>

<style scoped>
.drag{
    position: relative;
    background-color: #e8e8e8;
    width: 320px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    margin: 0 auto;
    margin-top: 10px;
    overflow: hidden;
}
.big-img{
    width: 320px;
    height: 160px;
    margin: 0 auto;
    position: relative;
    overflow: hidden;
}
.big-img img{
    width: 320px;
    height: 160px;
}
.big-img .resetIcon{
    position: absolute; 
    top: 0px;
    right: 0px;
    z-index: 10px;
    width: 30px;
    height: 30px;
    font-size: 14px !important;
    background: url('../../assets/image/icon_light.c72616a.png') no-repeat 0px -299px;
    background-size: 32px 544px;
}
.shareClass{
    width: 24px;
    height: 24px;
    line-height: 24px;
    text-align: center;
    border: 3px solid white;
    border-radius: 50%;
    background-color: #539ffe;
    font-size: 18px;
    font-weight: bold;
    color: white;
    text-align: center;
    position: absolute;
    z-index: 199;
}
.firstIcon{
    position: absolute;
    left: 10px;
    top: 10px;
    z-index: 100;
}

.secondIcon{
    position: absolute;
    left: 40px;
    top: 10px;
    z-index: 101;
}
.thirdIcon{
    position: absolute;
    left: 70px;
    top: 10px;
    z-index: 102;
}
.fourthIcon{
    position: absolute;
    left: 100px;
    top: 10px;
    z-index: 103;
}
.fifthIcon{
    position: absolute;
    left: 130px;
    top: 10px;
    z-index: 104;
}
.liucheng{
    width: 9.5rem;
    margin: 0 auto;
}
.liucheng img{
    width: 100%;
}
</style>


