<template>
    <div>
        <div class="drag" ref="dragDiv">
            <div style="opacity:0;position:absolute;bottom:0;" id="qrcode"></div>
            <div class="drag_bg" ref="drag_bg"></div>
            <div class="drag_text" ref="drag_text">{{confirmWords}}</div>
            <div ref="moveDiv" 
              @touchstart="mousedownFn($event)" @touchmove='mouseMoveFn($event)' @touchend='moseUpFn($event)'
              @mousedown="mousedownFn($event)" @mousemove='mouseMoveFn($event)' @mouseup='moseUpFn($event)' :class="{'handler_ok_bg':confirmSuccess}"  
              class="handler handler_bg" 
              style="position: absolute;top: 0px;left: 0px;">
            </div>
        </div>
        <div class="btn">
            <button @click="resetEvent">重置</button>
        </div>
        <div class="liucheng">
            <img src="../../assets/image/拖动滑块验证流程图(1).png" alt="">
        </div>
    </div>
    
</template>

<script>
    export default {
        data(){
            return {
                beginClientX:0,           /*距离屏幕左端距离*/
                mouseMoveStata:false,     /*触发拖动状态  判断*/
                maxwidth:'',               /*拖动最大宽度，依据滑块宽度算出来的*/
                confirmWords:'拖动滑块验证',   /*滑块文字*/
                confirmSuccess:false           /*验证成功判断*/
                /* 首先根据时间搓来获取验证图需要的参数 */ 
            }
        },
        methods: {
            mousedownFn:function (e) {
                if(!this.confirmSuccess){
                    e.preventDefault && e.preventDefault();   //阻止文字选中等 浏览器默认事件
                    this.mouseMoveStata = true;
                    this.beginClientX = e.clientX|| e.changedTouches[0].clientX;
                }
            },        //mousedoen 事件
            successFunction(){
                this.confirmSuccess = true
                this.confirmWords = '验证通过';
                // if(window.addEventListener){
                //     document.getElementsByTagName('html')[0].removeEventListener('touchmove',this.mouseMoveFn);
                //     document.getElementsByTagName('html')[0].removeEventListener('touchend',this.moseUpFn);
                //     document.getElementsByTagName('html')[0].removeEventListener('mousemove',this.mouseMoveFn);
                //     document.getElementsByTagName('html')[0].removeEventListener('mouseup',this.moseUpFn);
                // }else {
                //     document.getElementsByTagName('html')[0].removeEventListener('touchend',()=>{});
                //     document.getElementsByTagName('html')[0].removeEventListener('mouseup',()=>{});
                // }
                this.$refs.drag_text.style.color = '#fff';
                // document.getElementsByClassName('handler')[0].style.left = this.maxwidth + 'px';
                //document.getElementsByClassName('drag_bg')[0].style.width = this.maxwidth + 'px';
                this.$refs.moveDiv.style.left = this.maxwidth + 'px';
                this.$refs.drag_bg.style.width = this.maxwidth + 'px';
                
            },                //验证成功函数
            mouseMoveFn(e){
                if(this.mouseMoveStata){
                    let clientX = e.clientX|| e.changedTouches[0].clientX;
                    let width = clientX- this.beginClientX;
                    if(width>0 && width<=this.maxwidth){
                        this.$refs.moveDiv.style.left = width + 'px';
                        this.$refs.drag_bg.style.width = width + 'px';
                    }else if(width>this.maxwidth){
                        console.log('成功')
                        this.successFunction();
                    }
                }
            },                   //mousemove事件
            moseUpFn(e){
                this.mouseMoveStata = false;
                let clientX =e.clientX|| e.changedTouches[0].clientX;
                var width = clientX - this.beginClientX;
                //console.log(width)
                if(width<this.maxwidth){
                    this.$refs.moveDiv.style.left = 0 + 'px';
                    this.$refs.drag_bg.style.width = 0 + 'px';
                    // document.getElementsByClassName('handler')[0].style.left = 0 + 'px';
                    // document.getElementsByClassName('drag_bg')[0].style.width = 0 + 'px';
                }
            } ,                      //mouseup事件
            resetEvent(){//重置
                this.confirmSuccess = false;
                this.mouseMoveStata = false;
                this.confirmWords = '拖动滑块验证';
                this.maxwidth = this.$refs.dragDiv.clientWidth - this.$refs.moveDiv.clientWidth;
                console.log(this.maxwidth)
                this.$refs.moveDiv.style.left = 0 + 'px';
                this.$refs.drag_bg.style.width = 0 + 'px';
                // document.getElementsByClassName('handler')[0].style.left = '0px';
                // document.getElementsByClassName('drag_bg')[0].style.width =  '0px';
                // document.getElementsByTagName('html')[0].addEventListener('touchmove',this.mouseMoveFn);
                // document.getElementsByTagName('html')[0].addEventListener('touchend',this.moseUpFn)
                // document.getElementsByTagName('html')[0].addEventListener('mousemove',this.mouseMoveFn);
                // document.getElementsByTagName('html')[0].addEventListener('mouseup',this.moseUpFn)
            }
        },
        mounted(){
            this.maxwidth = this.$refs.dragDiv.clientWidth - this.$refs.moveDiv.clientWidth;
            console.log(this.maxwidth)
            // document.getElementsByTagName('html')[0].addEventListener('touchmove',this.mouseMoveFn);
            // document.getElementsByTagName('html')[0].addEventListener('touchend',this.moseUpFn)
            // document.getElementsByTagName('html')[0].addEventListener('mousemove',this.mouseMoveFn);
            // document.getElementsByTagName('html')[0].addEventListener('mouseup',this.moseUpFn)
        },
    }
</script>

<style scoped>
    .drag{
        position: relative;
        background-color: #e8e8e8;
        width: 9rem;
        height: 34px;
        line-height: 34px;
        text-align: center;
        margin: 0 auto;
    }
    .handler{
        width: 40px;
        height: 32px;
        border: 1px solid #ccc;
        cursor: move;
    }
    .handler_bg{
        background: #fff url("data:image/png;base64,") no-repeat center;
    }
    .handler_ok_bg{
        background: #fff url("data:image/png;base64,") no-repeat center;
    }
    .drag_bg{
        background-color: #7ac23c;
        height: 34px;
        width: 0px;
    }
    .drag_text{
        position: absolute;
        top: 0px;
        width: 100%;text-align: center;
        -moz-user-select: none;
        -webkit-user-select: none;
        user-select: none;
        -o-user-select:none;
        -ms-user-select:none;
    }
    .btn{
        text-align: center;
        margin-top: 10px;
    }
    button{
        background: #c9c9c9;
        text-align: center;
        padding: 2px 10px;
    } 
    .liucheng{
        width: 9.5rem;
        margin: 0 auto;
    }
    .liucheng img{
        width: 100%;
    }
</style>


