<template>
    <div>
        <div class="big-img">
          <img :src="list[num].bigImgurl" alt="">
          <img :src="list[num].smallImgurl" ref="smallBig" class="huakaui" alt="">
          <button @click="resetEvent">重置</button>
        </div>

         <div class="drag" ref="dragDiv">
            <div style="opacity:0;position:absolute;bottom:0;" id="qrcode"></div>
            <div class="drag_bg" ref="drag_bg"></div>
            <div class="drag_text" ref="drag_text">{{confirmWords}}</div>
            <div ref="moveDiv" 
              @touchstart="mousedownFn($event)" @touchmove='mouseMoveFn($event)' @touchend='moseUpFn($event)'
              @mousedown="mousedownFn($event)" @mousemove='mouseMoveFn($event)' @mouseup='moseUpFn($event)' :class="{'handler_ok_bg':confirmSuccess}"  
              class="handler handler_bg" 
              style="position: absolute;top: 0px;left: 0px;">
            </div>
        </div>
        <div class="liucheng">
            <img src="../../assets/image/滑动拼图验证流程图.png" alt="">
        </div>
    </div>
    
</template>

<script>
    export default {
        data(){
            return {
                l : 42, // 滑块边长
                r : 9, // 滑块半径
                w : 320, // canvas宽度
                h : 155, // canvas高度
                PI : Math.PI,
                L : this.l + this.r * 2 + 3 ,// 滑块实际边长
                isIE : window.navigator.userAgent.indexOf('Trident') > -1,
                beginClientX:0,           /*距离屏幕左端距离*/
                mouseMoveStata:false,     /*触发拖动状态  判断*/
                maxwidth:'',               /*拖动最大宽度，依据滑块宽度算出来的*/
                confirmWords:'向右滑动填充拼图',   /*滑块文字*/
                confirmSuccess:false,           /*验证成功判断*/
                list:[
                    {
                        bigImgurl:require('../../assets/image/c64486977b494ed2bfaa1fb6925567f4.jpg'),
                        smallImgurl:require('../../assets/image/3bb81fd08b924b4897b025ea219d0b45.png'),
                        maxWidth:'',
                        minWidth:'',
                    },  
                ],
                num:0,
            }
        },
        methods: {
          mousedownFn:function (e) {
              if(!this.confirmSuccess){
                  e.preventDefault && e.preventDefault();   //阻止文字选中等 浏览器默认事件
                  this.mouseMoveStata = true;
                  this.beginClientX = e.clientX|| e.changedTouches[0].clientX;
              }
          },        //mousedoen 事件
          successFunction(){
                this.confirmSuccess = true
                this.confirmWords = '拼图成功';
                console.log(this.confirmWords)
                this.$refs.drag_text.style.color = '#fff'
            },                //验证成功函数
            mouseMoveFn(e){
                if(this.mouseMoveStata){
                    let clientX = e.clientX|| e.changedTouches[0].clientX;
                    let width = clientX- this.beginClientX;
                    let multiple = (this.w - 40 - 16)/(this.w - 40);
                    if(!this.confirmSuccess){
                        if(width>0 && width<this.maxwidth){
                            //console.log('kfgfg')
                            this.$refs.smallBig.style.left = multiple*width + 'px';
                            this.$refs.moveDiv.style.left = width + 'px';
                            this.$refs.drag_bg.style.width = width + 'px';
                        }
                    }
                    
                }
            },                   //mousemove事件
            moseUpFn(e){
                this.mouseMoveStata = false;
                let clientX =e.clientX|| e.changedTouches[0].clientX;
                let multiple = (this.w - 40 - 16)/(this.w - 40)
                var width = clientX - this.beginClientX;
                //console.log(width)
                if(!this.confirmSuccess){
                    if(width<80||width>95){
                        this.$refs.smallBig.style.left = 0+ 'px';
                        this.$refs.moveDiv.style.left = 0 + 'px';
                        this.$refs.drag_bg.style.width = 0 + 'px';
                    }else{
                        //console.log(width*multiple)
                        this.$refs.smallBig.style.left = width*multiple + 'px';
                        this.$refs.moveDiv.style.left = width + 'px';
                        this.$refs.drag_bg.style.width = width + 'px';
                        this.confirmSuccess = true;
                        this.successFunction();
                    }
                }
                
            } ,                      //mouseup事件
            resetEvent(){//重置
                this.confirmSuccess = false;
                this.mouseMoveStata = false;
                this.confirmWords = '向右滑动填充拼图';
                this.maxwidth = this.$refs.dragDiv.clientWidth - this.$refs.moveDiv.clientWidth;
                console.log(this.maxwidth)
                this.$refs.smallBig.style.left = 0+ 'px';
                this.$refs.moveDiv.style.left = 0 + 'px';
                this.$refs.drag_bg.style.width = 0 + 'px';
                // document.getElementsByTagName('html')[0].addEventListener('touchmove',this.mouseMoveFn);
                // document.getElementsByTagName('html')[0].addEventListener('touchend',this.moseUpFn)
                // document.getElementsByTagName('html')[0].addEventListener('mousemove',this.mouseMoveFn);
                // document.getElementsByTagName('html')[0].addEventListener('mouseup',this.moseUpFn)
            }
        },
        mounted(){
          this.maxwidth = this.$refs.dragDiv.clientWidth - this.$refs.moveDiv.clientWidth;
          console.log(this.maxwidth)
          // document.getElementsByTagName('html')[0].addEventListener('touchmove',this.mouseMoveFn);
          // document.getElementsByTagName('html')[0].addEventListener('touchend',this.moseUpFn)
          // document.getElementsByTagName('html')[0].addEventListener('mousemove',this.mouseMoveFn);
          // document.getElementsByTagName('html')[0].addEventListener('mouseup',this.moseUpFn)
        }
    }
</script>

<style scoped>
 .drag{
        position: relative;
        background-color: #e8e8e8;
        width: 320px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        margin: 0 auto;
        margin-top: 10px;
        overflow: hidden;
    }
    .handler{
        width: 40px;
        height: 38px;
        border: 1px solid #ccc;
        cursor: move;
    }
    .handler_bg{
        background: #fff url("data:image/png;base64,") no-repeat center;
    }
    .handler_ok_bg{
        background: #fff url("data:image/png;base64,") no-repeat center;
    }
    .drag_bg{
        background-color: #7ac23c;
        height: 40px;
        width: 0px;
    }
    .drag_text{
        position: absolute;
        top: 0px;
        width: 100%;text-align: center;
        -moz-user-select: none;
        -webkit-user-select: none;
        user-select: none;
        -o-user-select:none;
        font-size: 14px;
        -ms-user-select:none;
    }
.big-img{
  width: 320px;
  height: 160px;
  margin: 0 auto;
  position: relative;
}
.huakaui{
  position: absolute;
  top: 0px;
  height: 100%;
  left: 0px;
  z-index: 10px;
}
.big-img button{
  position: absolute; 
  top: 0px;
  right: 0px;
  z-index: 10px;
  width: 30px;
  height: 20px;
  font-size: 14px !important;
}
 .liucheng{
        width: 9.5rem;
        margin: 0 auto;
    }
    .liucheng img{
        width: 100%;
    }
</style>


