<template>
    <div>
        <div ref="mescroll" class="mescroll" id="mescroll">
            <div v-if="componentshow" style="padding:0 0px;">
                <div class="news-list-wrap" v-for="(item,index) in dataList" :key='index'>
                    <!-- <modular-first ></modular-first> -->
                    <!-- <pc-huadong v-if="indexBiao==0"></pc-huadong> -->
                    <!-- <modular-second ></modular-second> -->
                    <yd-huadong v-if="indexBiao==1"></yd-huadong>
                    <hua-kuai v-if="indexBiao==2"></hua-kuai>
                    <point-selection v-if="indexBiao==3"></point-selection>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import modularFirst from './modularFirst';
import modularSecond from './modularSecond';
import pcHuadong from '../huadong/pchuadong';
import ydHuadong from '../huadong/ydHuadong';
import huaKuai from '../huadong/huakuai';
import pointSelection from '../huadong/pointSelection';
    
export default {
    components: {modularFirst,modularSecond,pcHuadong,ydHuadong,huaKuai,pointSelection},
    data(){
        return{
            type: "",
            name: "",
            timer: null,
            loadingtimes: 0,
            once: true,
            dataList: [1],
            indexBiao:0,
            datalistAd: [],
            itemImagelazy: [], //懒加载存储图片数据
            componentshow: true, //组件是否展示
            idRequest:true,
            adHtml: [
                // "//tu.baixing.com/widget/w/zl-xxlst26.html?autosize=1",
                // "//tu.baixing.com/widget/w/zl-xxlst27.html?autosize=1",
                // "//tu.baixing.com/widget/w/zl-xxlst28.html?autosize=1",
                // "//tu.baixing.com/widget/w/zl-xxlst29.html?autosize=1",
                // "//tu.baixing.com/widget/w/zl-xxlst30.html?autosize=1",
            ],
            newList: ['拖动滑块验证','滑动拼图验证','文字点选验证'],
            iframeheight: '',
            topimage: {
                src: require("../../assets/image/mescroll-totop.png")
            },
            OnceTimes: 3000,
        }
    },
    mounted(){
        this.componentshow = true;
        if(this.$route.params.type=='拖动滑块验证'){
            this.indexBiao = 1;
        }else if(this.$route.params.type=='滑动拼图验证'){
            this.indexBiao= 2;
        }else if(this.$route.params.type=='文字点选验证'){
            this.indexBiao= 3;
        }
    },
    watch: {
    // 监听路由变化-用于非首次加载
        $route(to, from) {
            if(to.params.type=='拖动滑块验证'){
                this.indexBiao = 1;
            }else if(to.params.type=='滑动拼图验证'){
                this.indexBiao= 2;
            }else if(to.params.type=='文字点选验证'){
                this.indexBiao= 3;
            }
        }
    }
}
</script>

<style lang="scss" scoped="">
@import "@/assets/styles/common.scss";
//三图的样式

.mescroll {
 margin: 0 auto;
 overflow: hidden;
 margin: 10px 0px;
}
</style>



